<template>
  <div class="flex flex-col justify-center items-center h-full w-full">
    <div
      class="
        bg-white
        rounded-2xl
        shadow-xl
        p-3
        m-4
        w-full
        md:w-11/12
        lg:max-w-5xl
      "
    >
      <h2 class="text-gray-500 text-2xl text-center">ToDo List</h2>
      <div class="flex mt-4">
        <input
          type="text"
          v-model="task"
          placeholder="請輸入資料，限制10個字數"
          class="shadow border rounded-xl w-full p-2 mr-4 text-gray-500"
          maxlength="10"
        />
        <button
          class="
            p-2
            border-2
            rounded-lg
            transition
            duration-500
            font-medium
            text-green-400
            border-green-400
            hover:text-white
            hover:bg-green-400
          "
          @click="submit"
        >
          {{ this.btn_text[this.ch_btn] }}
        </button>
      </div>
      <table class="table-fixed my-5 w-full">
        <thead class="bg-gray-100">
          <tr class="w-full">
            <th class="text-center w-6/12">內容</th>
            <th class="text-center w-2/12">狀態</th>
            <th class="text-center w-2/12">刪除</th>
            <th class="text-center w-2/12">修改</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in tasks" :key="index">
            <!-- 內容 -->
            <td>
              <!-- 如果顯示finish則顯示刪除線 -->
              <span :class="{ 'line-through': item.status === 'finish' }">
                {{ item.name }}
              </span>
            </td>
            <!-- 狀態+點擊後變更狀態 -->
            <td class="hover:bg-gray-200" @click="chColor(index)">
              <span
                class="noSelect cursor-default p-1"
                :class="{
                  'text-blue-500': item.status === 'wait',
                  'text-red-500': item.status === 'working',
                  'text-green-500': item.status === 'finish',
                }"
              >
                {{ item.status }}</span
              >
            </td>
            <!-- 刪除 -->
            <td class="text-center">
              <div @click="delTask(index)">
                <span
                  class="
                    fa fa-trash
                    pointer
                    transition
                    duration-300
                    hover:text-red-500
                  "
                ></span>
              </div>
            </td>
            <!-- 編輯 -->
            <td class="text-center">
              <div @click="updTask(index)">
                <p
                  class="
                    fa fa-pen
                    pointer
                    transition
                    duration-300
                    hover:text-blue-500
                  "
                ></p>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  name: "TodoList",
  data() {
    return {
      task: "",
      editMsg: null,
      statuses: ["wait", "working", "finish"],
      btn_text: ["新增", "修改"],
      ch_btn: 0,
      tasks: [
        {
          name: "AAAaa11111",
          status: "wait",
        },
        {
          name: "bbbBB22222",
          status: "working",
        },
        {
          name: "CCCcc33333",
          status: "finish",
        },
      ],
    };
  },
  methods: {
    // 核對狀態變更字體顏色
    chColor(index) {
      let newIndex = this.statuses.indexOf(this.tasks[index].status);

      if (++newIndex > 2) newIndex = 0;

      this.tasks[index].status = this.statuses[newIndex];
    },
    // 刪除
    delTask(index) {
      this.tasks.splice(index, 1);
    },
    // 更新
    updTask(index) {
      this.task = this.tasks[index].name;
      this.editMsg = index;
      this.ch_btn = 1;
    },
    // 送出
    submit() {
      // 禁止空白
      if (this.task.length === 0) return;

      if (this.editMsg != null) {
        // 修改
        this.tasks[this.editMsg].name = this.task;
        this.editMsg = null;
        this.ch_btn = 0;
      } else {
        // 新增
        this.tasks.push({
          name: this.task,
          status: "wait",
        });
      }
      this.task = "";
    },
  },
};
</script>